:root {
  --txt-black: #0F2137;
  --txt-black-secondary: #343D48;
  --txt-blur: #244886;
  --bg-gr-prime: linear-gradient(105.18deg, #1E3C72 -2.04%, #2A5298 107.42%);
}

/* dm-sans-regular - latin */

@font-face {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/dm-sans/dm-sans-v6-latin-regular.woff2") format("woff2"), url("../fonts/dm-sans/dm-sans-v6-latin-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* dm-sans-500 - latin */

@font-face {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/dm-sans/dm-sans-v6-latin-500.woff2") format("woff2"), url("../fonts/dm-sans/dm-sans-v6-latin-500.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* dm-sans-700 - latin */

@font-face {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/dm-sans/dm-sans-v6-latin-700.woff2") format("woff2"), url("../fonts/dm-sans/dm-sans-v6-latin-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

:root {
  --content-width: 1250px;
  --gutter: 15px;
  --mob-padding: 20px;
}

.container {
  max-width: var(--content-width);
  padding-left: var(--mob-padding);
  padding-right: var(--mob-padding);
  margin-left: auto;
  margin-right: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
}

html {
  font-family: "DM Sans";
  line-height: 1.4;
}

body.content-hidden {
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 25px;
  cursor: pointer;
  color: #fff;
  z-index: 9;
}

.burger_closed .burger__line:nth-child(1) {
  transform: translate(0, 12px) rotate(45deg);
}

.burger_closed .burger__line:nth-child(2) {
  display: none;
}

.burger_closed .burger__line:nth-child(3) {
  transform: translate(0, -12px) rotate(-45deg);
}

.burger__line {
  height: 2px;
  background: currentColor;
  transition: 0.3s;
}

.btn {
  display: inline-block;
  border: 1.5px solid #ffffff;
  color: #fff;
  padding: 13px 30px;
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border-radius: 50px;
  appearance: none;
}

.btn_fill {
  background-color: #fff;
  color: var(--txt-black);
}

.menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu > .menu-item.menu-item-has-children > a::after {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  border-left: 1px solid currentColor;
  border-bottom: 1px solid currentColor;
  margin-left: 5px;
  transform: translateY(-1px) rotate(-45deg);
  vertical-align: middle;
}

.menu .menu-item {
  position: relative;
}

.menu .menu-item:hover a {
  background: #fff;
  color: var(--txt-black);
}

.menu .menu-item:hover .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

.menu .menu-item > a {
  display: inline-block;
  color: currentColor;
  font-size: 16px;
  font-weight: normal;
  text-decoration: none;
  padding: 15px;
}

.menu .sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 150px;
  padding: 0;
  background: #fff;
  list-style-type: none;
  opacity: 0;
  visibility: hidden;
  transform: translate(0, -20px);
  transition: 0.3s;
}

.menu .sub-menu > .menu-item:hover > a {
  background: #f1f1f1;
}

.menu .sub-menu > .menu-item:hover .sub-menu {
  display: block;
}

.menu .sub-menu > .menu-item > a {
  width: 100%;
}

.menu .sub-menu .sub-menu {
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
}

.s-title {
  margin: 0;
  font-weight: bold;
  font-size: 36px;
  letter-spacing: -1.5px;
  text-align: center;
  line-height: 1.2;
}

.s-undertitle {
  font-weight: bold;
  font-size: 14px;
  color: var(--txt-blur);
  text-align: center;
  letter-spacing: 2.1px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin: 0;
  text-transform: uppercase;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 0;
  transition: 0.2s;
  color: #fff;
  z-index: 2;
}

.header_has-bg {
  background: #f1f1f1;
  color: var(--txt-black);
}

.header_menu-opend {
  color: var(--txt-black);
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo-wrap {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: currentColor;
}

.header__logo path {
  fill: currentColor;
}

.header__logo-text {
  font-weight: bold;
  font-size: 20px;
  margin-left: 10px;
  opacity: 0.8;
}

.header__cta {
  color: currentColor;
  border-color: currentColor;
}

.header__burger {
  color: currentColor;
}

.footer {
  background: #000;
  font-size: 24px;
  color: grey;
}

.promo {
  position: relative;
  padding-top: 180px;
  padding-bottom: 100px;
  min-height: 100vh;
  background-image: url(../img/promo/pattern.svg), var(--bg-gr-prime);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  border-radius: 0 0 13% 0/0 0 20% 0;
}

.promo__inner {
  max-width: 650px;
}

.promo__content-wrap {
  position: relative;
  z-index: 1;
}

.promo__title {
  font-weight: bold;
  font-size: 85px;
  line-height: 1.2;
  letter-spacing: -2px;
  margin-top: 0;
  margin-bottom: 30px;
}

.promo__subtitle {
  font-weight: 500;
  font-size: 18px;
  max-width: 510px;
  margin-bottom: 38px;
  line-height: 200%;
}

.promo__cta {
  margin-right: 20px;
}

.promo__btn-watch {
  font-weight: 500;
  font-size: 16px;
  color: #fff;
  background: transparent;
  border: none;
  cursor: pointer;
}

.promo__btn-watch:hover::before {
  transform: scale(1.1) rotate(125deg);
}

.promo__btn-watch::before {
  content: url("data:image/svg+xml,%3Csvg width='25' height='26' viewBox='0 0 25 26' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M12.5 0.997559C5.5965 0.997559 0 6.59434 0 13.497C0 20.4018 5.5965 25.9981 12.5 25.9981C19.4035 25.9981 25 20.4018 25 13.497C25 6.59434 19.4035 0.997559 12.5 0.997559ZM16.8203 14.1599L10.5703 18.0669C10.4439 18.1459 10.3 18.1852 10.1563 18.1852C10.0261 18.1852 9.89548 18.1524 9.77749 18.0868C9.52916 17.9497 9.37502 17.6873 9.37502 17.4046V9.59165C9.37502 9.30776 9.52916 9.04593 9.77749 8.90831C10.0258 8.7696 10.3295 8.77767 10.5703 8.92929L16.8203 12.8346C17.0486 12.9782 17.1875 13.2287 17.1875 13.497C17.1875 13.7669 17.0486 14.018 16.8203 14.1599Z' fill='white'/%3E%3C/svg%3E");
  display: inline-block;
  margin-right: 10px;
  vertical-align: middle;
  transition: 0.3s;
}

.promo__sponsors {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 40px;
}

.promo__sponsors-txt {
  font-weight: normal;
  font-size: 16px;
  opacity: 0.6;
  margin-right: 20px;
  margin-bottom: 5px;
}

.promo__sponsor-ico {
  margin-right: 28px;
  margin-bottom: 5px;
}

.promo__bg-wrap {
  position: absolute;
  right: 40px;
  top: 50%;
  width: 50%;
  transform: translate(0, -45%);
  pointer-events: none;
}

.promo__bg-wrap::before {
  content: "";
  position: absolute;
  top: -40px;
  right: 40%;
  width: 300px;
  height: 350px;
  background: url(../img/promo/pattern.png);
  background-size: contain;
  background-repeat: no-repeat;
  z-index: -1;
  transform: translate(50%);
}

.promo__phone-img {
  margin-left: auto;
}

.about {
  padding-top: 65px;
  padding-bottom: 100px;
}

.about__title {
  margin-bottom: 100px;
}

.about__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 100px;
  padding-left: 100px;
  padding-right: 100px;
}

.about__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.about__card-ico {
  height: 110px;
  margin-bottom: 20px;
}

.about__card-title {
  font-weight: bold;
  font-size: 18px;
  margin-top: 0;
  margin-bottom: 20px;
}

.about__card-desc {
  font-weight: normal;
  font-size: 15px;
  margin-top: 0;
  margin-bottom: 0;
  color: var(--txt-black-secondary);
}

.app {
  position: relative;
  padding-top: 100px;
  padding-bottom: 80px;
}

.app::before {
  content: url(../img/app/pattern.svg);
  position: absolute;
  max-width: 100%;
  overflow: hidden;
  top: 230px;
  left: 0;
  z-index: -1;
}

.app__row {
  align-items: center;
}

.app__img-block {
  box-sizing: border-box;
  margin-right: var(--gutter);
  margin-left: var(--gutter);
  width: calc(100% * 0.5 - var(--gutter) * 2);
  padding-right: 30px;
}

.app__phone-img {
  margin-left: auto;
}

.app__info-block {
  box-sizing: border-box;
  margin-right: var(--gutter);
  margin-left: var(--gutter);
  width: calc(100% * 0.5 - var(--gutter) * 2);
  padding-left: 50px;
  padding-right: 30px;
}

.app__undertitle {
  margin-bottom: 10px;
  text-align: left;
}

.app__title {
  margin-bottom: 30px;
  text-align: left;
}

.app__pluses {
  padding-left: 0;
  margin: 0;
  list-style: none;
}

.app__plus {
  position: relative;
  margin-bottom: 10px;
  background: #ffffff;
  padding-top: 25px;
  padding-right: 35px;
  padding-bottom: 25px;
  padding-left: 100px;
  border-radius: 16px;
  transition: 0.3s;
}

.app__plus:last-child {
  margin-bottom: 0;
}

.app__plus:hover {
  box-shadow: 0px 8px 24px rgba(69, 88, 157, 0.07);
}

.app__plus-ico {
  position: absolute;
  top: 25px;
  left: 25px;
}

.app__plus-title {
  font-weight: bold;
  font-size: 18px;
  margin-top: 0;
  margin-bottom: 20px;
}

.app__plus-desc {
  font-weight: normal;
  font-size: 15px;
  line-height: 1.8;
  color: var(--txt-black-secondary);
  margin-top: 0;
  margin-bottom: 0;
}

@media (max-width: 1200px) {
  .promo {
    min-height: unset;
  }

  .promo__title {
    font-size: 65px;
  }

  .promo__bg-wrap {
    top: 40%;
  }

  .promo__bg-wrap::before {
    width: 250px;
    height: 300px;
  }
}

@media (max-width: 992px) {
  .burger {
    display: flex;
  }

  .menu {
    flex-direction: column;
  }

  .menu .menu-item:hover a {
    background: transparent;
  }

  .menu .menu-item > a {
    font-size: 22px;
  }

  .menu .sub-menu {
    position: static;
    opacity: 1;
    visibility: visible;
    transform: translate(0, 0);
    background: transparent;
    padding-left: 30px;
  }

  .menu .sub-menu > .menu-item:hover > a {
    background: transparent;
  }

  .menu .sub-menu > .menu-item > a {
    font-size: 18px;
  }

  .menu .sub-menu .sub-menu {
    display: block;
    position: static;
    padding-left: 20px;
  }

  .s-title {
    font-size: 28px;
  }

  .header__logo-wrap {
    position: relative;
    z-index: 15;
  }

  .header__menu-container {
    display: none;
    position: fixed;
    padding-top: 80px;
    padding-right: 25px;
    padding-bottom: 0;
    padding-left: 25px;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #f1f1f1;
    z-index: 9;
  }

  .header__menu-container_visible {
    display: block;
  }

  .header__nav {
    height: 100%;
    overflow-y: auto;
  }

  .header__cta {
    position: relative;
    margin-left: auto;
    margin-right: 30px;
    z-index: 15;
  }

  .promo {
    border-radius: 0;
  }

  .promo__inner {
    max-width: 600px;
  }

  .promo__title {
    font-size: 45px;
  }

  .promo__bg-wrap {
    position: static;
    transform: translate(0);
    width: 100%;
    pointer-events: visible;
  }

  .about {
    padding-bottom: 60px;
  }

  .about__title {
    margin-bottom: 60px;
  }

  .about__grid {
    grid-template-columns: 1fr;
  }

  .about__card {
    margin-bottom: 40px;
  }

  .app {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .app__img-block {
    width: calc(100% * 1 - var(--gutter) * 2);
    order: 1;
  }

  .app__phone-img {
    margin-right: auto;
  }

  .app__info-block {
    width: calc(100% * 1 - var(--gutter) * 2);
  }
}

@media (max-width: 640px) {
  .btn {
    font-size: 14px;
    padding: 10px 20px;
  }

  .s-title {
    font-size: 24px;
  }

  .promo {
    padding-top: 100px;
    padding-bottom: 60px;
  }

  .about__grid {
    padding-left: 40px;
    padding-right: 40px;
  }

  .app {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .app__img-block {
    padding-right: 0;
    padding-top: 20px;
  }

  .app__info-block {
    padding-left: 0;
    padding-right: 0;
  }

  .app__plus {
    padding-left: 30px;
  }

  .app__plus-ico {
    position: static;
    margin-bottom: 10px;
  }
}